import { HCF, HLink, HLocalStorage, HUtils } from "@macashipo/mlib"
import React from "react"
import { Button, Card, CardBody } from "reactstrap"
import { HConfig } from "src/core/helpers"
import { fnListConfigApp } from "src/helpers/fnList"
export default function Welcome() {
  console.warn("render welcome")
  HConfig.changeTitle("Welcome", "Welcome")
  //check first
  var screenLocal = HLocalStorage.getString("FirstLoad", "")
  if (screenLocal === "") {
    console.warn("need reload")
    HLocalStorage.saveString("FirstLoad", "1")
    HUtils.runFuntion(HCF.CF, "fnUpdateRouteFromLocalCategoryScreen", [])
  }
  let _authInfo = fnListConfigApp.getAuthInfo()
  let _name = ""
  if (_authInfo) {
    _name = _authInfo.name
  }
  return (
    <Card className="col-12 col-sm-8 col-xl-6" style={{ margin: "0px auto" }}>
      <CardBody className="">
        <div style={{ fontSize: "1.25rem", fontWeight: "bold" }}>
          Welcome {_name}
        </div>
        <div style={{ fontSize: "1.25rem" }}>Have a good day!</div>
        <div>
          <Button
            color="primary"
            onClick={() => {
              HLink.push(fnListConfigApp.getHomeUrl())
            }}
          >
            Home page
          </Button>
        </div>
      </CardBody>
    </Card>
  )
}
